import React, { Component } from 'react';
import './Section.css';


class SectionTwo extends Component {
    render() { 
        return ( 
            <section style={{backgroundColor: 'tan', marginTop: '50px'}}>
            <div className="container" style={{padding: '50px'}}>
                <div className="columns w3-animate-opacity">
                    <div className="column is-one-half">
                        <h2 style={{color: 'black'}}>LM Holdings</h2>
                        <hr className="new4"></hr> 
                    </div>
                    <div className="column">
                        <p className="mainText2">
                            Our aggregation model goes over and above a typical, traditional aggregation model; we provide comprehensive support 
                            to the smallholder farmer that includes, but not limited to, formalizing their businesses, 
                            sourcing affordable production funding, facilitation training and securing profitable markets.
                        </p>
                    </div>
                </div>
            </div>
        </section>
         );
    }
}
 
export default SectionTwo;