import React, { Component } from 'react';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';
import Group from '../../assets/group.jpg';
import Field from '../../assets/fieldguy.jpg';
import Fields from '../../assets/field.jpg';
import Miners from '../../assets/miners.jpg';
import Coal from '../../assets/bags open.jpg';
import Coals from '../../assets/bags.jpg';
import CoalMine from '../../assets/field.jpg';
import './Gallery.css';

class Gallery extends Component {
    render() {

        return ( 
            <div>
                <div className="container" style={{alignContent: 'center', padding: '50px', marginTop: '35px'}}>
                <h1 className="title1"><strong>Gallery</strong></h1>
                <hr className="new55"></hr>
                <br></br>
                <br></br>
                    <div className="columns">
                        <div className="column is-one-quarter">
                            <img src={Coals} alt="Coal mine" style={{width: "100%", borderRadius: '5px'}}/> 
                        </div>
                        <div className="column">
                            <img src={CoalMine} alt="Coal mine" style={{width: "100%", borderRadius: '5px'}}/>
                        </div>
                        <div className="column">
                            <img src={Coal} alt="Coal mine" style={{width: "100%", borderRadius: '5px'}}/>
                        </div>
                        <div className="column">
                                <div className="carde">
                                    <img src={Fields} alt="Coal mine" style={{width: "100%", borderRadius: '5px'}}/>
                                </div>
                            </div>
                    </div>
                    <div className="columns">
                        

                            <div className="column w3-animate-zoom">
                                <div className="carde">
                                    <img src={Group} alt="Luleka and the team" style={{width: "100%", borderRadius: '5px'}}/>
                                </div>
                            </div>

                            <div className="column w3-animate-zoom">
                                <div className="carde">
                                    <img src={Field} alt="Sorghum Farmers" style={{width: "100%", borderRadius: '5px'}}/>
                                </div>
                            </div>

                            <div className="column w3-animate-zoom">
                                <div className="carde">
                                    <img src={Miners} alt="Field work" style={{width: "100%", borderRadius: '5px'}}/>
                                </div>
                            </div>
                        </div>
                        </div>

                <ScrollToTop showUnder={250} duration={1000} >
                    <img src={arrowUp} alt="Back to top" />
                </ScrollToTop>
            </div>
         );
    }
}

export default Gallery;