import React, { Component } from 'react';
import './Section.css';


class SectionThree extends Component {
    render() { 
        return ( 
            <section style={{backgroundColor: 'white', marginTop: '50px'}}>
            <div className="container" style={{padding: '50px'}}>
                <div className="columns w3-animate-opacity">
                    <div className="column is-one-half">
                        <h2 style={{color: 'black'}}>LM Agribusiness</h2>
                        <hr className="new2"></hr> 
                    </div>
                    <div className="column">
                        <p className="mainText">
                                An agri-input sales company committed to making good quality farm inputs affordable and accessible to black communal farmers. 
                                Having grown up in rural Eastern Cape and being raised by caregivers that are/were communal farmers, we are well attuned to the needs and challenges of communal farmers.
                                <br></br> 
                                Our solutions are driven by the needs of this market. As part of our aggregation effort we noticed that securing quality inputs at affordable prices was one of the biggest challenges to our communal farmers.  Therefore, we decided to harness the collective power of the farmers we have on our program that own over 7000 ha of land. 
                                With this powerful backing, we were able to negotiate exceptional prices for the inputs and most importantly we have the access to good quality products. 
                        </p>
                    </div>
                </div>
            </div>
        </section>
         );
    }
}
 
export default SectionThree;