import React, { Component } from 'react';
import './ContactSection.css';
import { Link } from 'react-router-dom';

class ContactSection extends Component {
    render() { 
        return ( 
            <div>
                <section style={{backgroundColor: 'rgb(116, 112, 64)', marginTop: '50px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-left">
                            <div className="column is-one-half">
                            <h2 style={{color: 'white'}}>Contact Us</h2>
                                    <hr className="new33"></hr>
                            </div>
                            <div className="column">
                                <p style={{color: 'white'}}>Click on the button below to get more information on how to get in touch with us.</p>

                                <br></br>
                                <br></br>

                                <Link to="/contact">
                                    <button className="btn-homes">Contact</button>
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
         );
    }
}
 
export default ContactSection;