import React, { Component } from 'react';
import './Section.css';
import Timeline from '../../assets/timeline.png';


class SectionFour extends Component {
    render() { 
        return ( 
            <section style={{backgroundColor: 'tan', marginTop: '50px'}}>
            <div className="container" style={{padding: '50px'}}>
                <div className="columns w3-animate-opacity">
                    <div className="column is-one-half">
                        <h2 style={{color: 'black'}}>LM Finance</h2>
                        <hr className="new4"></hr> 
                    </div>
                    <div className="column">
                        <p className="mainText2">
                            The funding is typically made up of a combination of a grant and soft loan, for example in the sorghum cluster,
                            Diageo Empowerment Trust provides grant funding for the first two years of production and thereafter 
                            LM Finance will provide soft loans using a wholesale facility.
                        </p>

                        <br></br>

                        <p className="mainText2">
                            This is designed to assist farmers to become accustomed to receiving, utilizing and 
                            paying back funds in order to develop the track record to access commercial finance from as they grow.
                            <br></br>
                            <br></br>
                            <img src={Timeline} alt="Timeline" style={{width: '100%'}}/>
                        </p>
                    </div>
                </div>
            </div>
        </section>
         );
    }
}
 
export default SectionFour;