import React from 'react';
import './App.css';
import Header from './components/Header/Header';
import Home from './components/Home/Home';
import Footer from './components/Footer/Footer';
import About from './components/About/About';
import Projects from './components/Projects/Projects';
import Contact from './components/Contact/Contact';
import Articles from './components/Articles/Articles';
import Gallery from './components/Gallery/Gallery';
import { HashRouter as Router, Route, Switch } from 'react-router-dom';
import { Helmet } from 'react-helmet';


function App() {
  return (
    <Router>
      <div className="App">
      <Helmet>
          <title>LM Holdings</title>
          <meta name="keywords" content="LM Holdings, agriculture, farming, sorghum, agribusiness" />
          <meta name="description" content="LM Holdings is a new entrant in the agricultural space, with its primary business being the production of sorghum in the rural areas of the Eastern Cape &amp; KwaZulu Natal." />
      </Helmet>
        <Header />
          <Switch>
            <Route exact={true} path="/" component={Home} />
            <Route exact path="/about" component={About} />
            <Route exact path="/projects" component={Projects} />
            <Route exact path="/articles" component={Articles} />
            <Route exact path="/gallery" component={Gallery} />
            <Route exact path="/contact" component={Contact} />
          </Switch>
        <Footer />
      </div>
    </Router>
  );
}

export default App;
