import React, { Component } from 'react';
import './ProjectSection.css';
import Coal from '../../assets/bags open.jpg';
import Coals from '../../assets/bags.jpg';
import CoalMine from '../../assets/field.jpg';

class ProjectSectionFour extends Component {
    render() { 
        return ( 
            <div>
                <section style={{backgroundColor: 'rgb(179, 109, 18)', marginTop: '50px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-zoom">
                            <div className="column is-one-half">
                                <h3 style={{color: 'black'}}>Charcoal Manufacturing</h3>
                                <hr className="new11"></hr> 
                            </div>
                            <div className="column">
                                <img className="cnt" src={Coal} alt="Coal bags" style={{width: '100%'}} />
                                <br></br>
                                <br></br>
                                <img className="cnt" src={Coals} alt="Open coal bags" style={{width: '100%'}} />
                                <br></br>
                                <br></br>
                                <img className="cnt" src={CoalMine} alt="Coal mine" style={{width: '100%'}} />
                            </div>
                        </div>
                    </div>
                </section>
            </div>
         );
    }
}
 
export default ProjectSectionFour;