import React, { Component } from 'react';
import './Articles.css';
import article1 from '../../assets/article1.jpg';
import article2 from '../../assets/article2.jpg';
import article3 from '../../assets/article3.jpg';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';

class Articles extends Component {
    render() { 
        return ( 
            <div>
                 <div className="container" style={{alignContent: 'center', padding: '50px', marginTop: '35px'}}>
                <h1 className="title1"><strong>Articles</strong></h1>
                <hr className="new55"></hr>
                <br></br>
                <br></br>
                    <div className="columns">
                        <div className="column is-one-third w3-animate-zoom">
                            <div className="carde">
                                <img src={article1} alt="Sorghum produce" style={{width: "100%", borderRadius: '5px'}}/>
                                <h1 className="title1">Farmer's Weekly</h1>
                                <br></br>
                                <button className="article"><a href="https://www.farmersweekly.co.za/agri-business/empowerment/entrepreneur-empowers-eastern-cape-communal-sorghum-farmers/" target="_blank" rel="noopener noreferrer">Read More</a></button>
                            </div>
                        </div>

                        <div className="column w3-animate-zoom">
                            <div className="carde">
                                <img src={article3} alt="Sorghum Farmers" style={{width: "100%", borderRadius: '5px'}}/>
                                <h1 className="title1">Sowetan Live</h1>
                                <br></br>
                                <button className="article"><a href="https://www.sowetanlive.co.za/sebenza-live/2019-05-10-heres-how-sorghum-brews-success-for-rural-farmers/" target="_blank" rel="noopener noreferrer">Read More</a></button>
                            </div>
                        </div>

                        <div className="column w3-animate-zoom">
                            <div className="carde">
                                <img src={article2} alt="Luleka Mbete" style={{width: "100%", borderRadius: '5px'}}/>
                                <h1 className="title1">Polity</h1>
                                <br></br>
                                <button className="article"><a href="https://www.polity.org.za/article/diageo-empowerment-trust-sa-lifts-emerging-black-ec-farmers-2019-09-05" target="_blank" rel="noopener noreferrer">Read More</a></button>
                            </div>
                        </div>
                    </div>
                </div>

                <br></br>
                <br></br>
                <br></br>
                <br></br>
                <br></br>

                <ScrollToTop showUnder={500} duration={1000} >
                    <img src={arrowUp} alt="Back to top" />
                </ScrollToTop>
            </div>
         );
    }
}
 
export default Articles;