import React, { Component } from 'react';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';
import './Contact.css'

class Contact extends Component {
   
    
    render() { 
        
        const email = () => {
            return window.location.href='mailto:lulekambete@lm-holdings.co.za';
        };

        return ( 
            <div>
                <div className="container">
                <section className="hero is-fullheight">
                    <div className="hero-body">
                        <div className="container has-text-centered">
                            <div className="columns">
                                <div className="column is-half has-text-left">
                                    <h1 className="title is-1">Contact Us</h1>
                                    <p className="is-size-5">To get in touch with us, click the button to send us an email and we will get back to you as soon as possible!</p>
                                    <br></br>
                                    <br></br>
                                    <p className="is-size-6"><a className="icons" href="#"><i className="fa fa-phone"></i></a> 072 029 1485</p>
                                    <p className="is-size-6"><a className="icons" href="#"><i className="fa fa-envelope"></i></a> lulekambete@lm-holdings.co.za</p>
                                </div>
                                <div className="column" style={{marginTop: '100px'}}>
                                    <div className="control">
                                        <button onClick={email} type="submit" className="button is-link is-fullwidth has-text-weight-medium is-medium">Send Message</button>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
	            </section>
                </div>

                <ScrollToTop showUnder={500} duration={1000} >
                    <img src={arrowUp} alt="Back to top" />
                </ScrollToTop>
            </div>
         );
         
    }
}


 
export default Contact;