import React, { Component } from 'react';
import './HomeSnippet.css';
import { Link } from 'react-router-dom';

class HomeSnippet extends Component {
    render() { 
        return ( 
            <div>
                <section style={{backgroundColor: 'white', marginTop: '50px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-left">
                            <div className="column is-one-half">
                                <h1 style={{color: 'black'}}>About The Company</h1>
                                <hr className="new44"></hr> 
                            </div>
                            <div className="column">
                                <p className="mainText">
                                    Established in 2016, LM Holdings (Pty) Ltd (“LMH”) is a new entrant in the agricultural space, 
                                    with its primary business being the production of sorghum in the rural areas of...
                                </p>
                                <br></br>
                                <br></br>

                                <Link to="/about">
                                    <button className="btn-cont">Read More</button>
                                </Link>

                                
                            </div>
                            </div>
                        </div>
                 </section>
            </div>
         );
    }
}
 
export default HomeSnippet;