import React, { Component } from 'react';
import './ProjectSection.css';

class ProjectSection extends Component {
    render() { 
        return ( 
            <div>
                <section style={{backgroundColor: 'white', marginTop: '50px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-zoom">
                            <div className="column is-one-half">
                                <h3 style={{color: 'black'}}>Mangwevini Sorghum Pilot Project</h3>
                                <hr className="new22"></hr> 
                            </div>
                            <div className="column">
                                <p className="mainText11">
                                    The Mangwevini Sorghum Project was the first project completed in the Eastern Cape during the 2016/17 summer season. 
                                    This was in essence a pilot project whose key object was to assess the feasibility of our 
                                    aggregation model and suitability of sorghum production in the Eastern Cape.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                    With this project, we were able to convert virgin lands into productive land parcels that yielded a crop which was then sold to 
                                    UNB.
                                    <br></br> 
                                    In addition, a total of 20 young women were employed and trained before, during and after the planting season.
                                    <br></br> 
                                    The successful rollout of the pilot resulted in UNB undertaking to purchase fixed annual quantities 
                                    from us for the next five years (until the 2024/25 season) and DETSA committed to financially support the project 
                                    during the same period.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
         );
    }
}
 
export default ProjectSection;