import React, { Component } from 'react';
import './Section.css';


class Section extends Component {
    render() { 
        return ( 
            <section style={{backgroundColor: 'white', marginTop: '50px'}}>
            <div className="container" style={{padding: '50px'}}>
                <div className="columns w3-animate-opacity">
                    <div className="column is-one-half">
                        <h1 style={{color: 'black'}}>About Us</h1>
                        <hr className="new2"></hr> 
                    </div>
                    <div className="column">
                        <p className="mainText">
                             Established in 2016, LM Holdings (Pty) Ltd (“LMH”) is a new entrant in the agricultural space, with its primary business being the production of sorghum in the rural areas of the Eastern Cape &amp; KwaZulu Natal with the aim of expanding to North West, Gauteng, Mpumalanga and Limpopo over the next 5 years.
                        </p>

                        <br></br>

                        <p  className="mainText">
                        Our aim is to assist rural farmers and communal land owners realize the potential of their land parcels and the significant role they can play in participating in the South African agricultural market. LMH partners with smallholder farmers, communal land owners and large businesses that are consumers of the sorghum product.
                        </p>

                        <br></br>

                        <p className="mainText">
                            LMH currently has in excess of 7300ha (5000 hectares in EC, 1000 in KZN, 300 in Mpumalanga, and 1000 Limpopo) of Mega Farms with the capacity to produce approximately 8000 tons of sorghum under contract production, 10 000 tons of maize. The collaborative partnership with the land owners has created the opportunity for previously disadvantaged communities to participate in commercial crop production.
                            In order to achieve its goals, LMH prides itself as being:

                            <div className="content">
                                <ul>
                                    <li>a 100% black-owned, woman-led, inclusive company for cross-sector collaboration</li>
                                    <li>a company that believes in responsible investment into African agriculture, with a strong drive for social upliftment and bringing dignity and hope back to rural areas</li>
                                    <li>a generating agriculture-driven economic growth that contributes to reducing poverty and hunger</li>
                                </ul>
                            </div>
                        </p>

                        <br></br>
                        
                        <p className="mainText">
                            In order to diversify its risk and create a broader product base for its farming partners, LMH also provides a range of other products, including bulk procurement of inputs 
                            (LM Agribusiness) Wholesale Finance &amp; Insurance (LM Finance), Investment in fruit farms &amp; 
                            processing of Cannabis Sativa- Hemp (LM Farms), LM Community Ignition Projects (manufacturing of household &amp; 
                            industrial charcoal, poles from the wattle harvested from these communal lands). Some of the proceeds from 
                            the sale of this charcoal will be ploughed back to the communities.
                        </p>
                    </div>
                </div>
            </div>
        </section>
         );
    }
}
 
export default Section;