import React, { Component } from 'react';
import ProjectSection from '../ProjectSections/ProjectSection';
import ProjectSectionTwo from '../ProjectSections/ProjectSectionTwo';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';
import ProjectSectionThree from '../ProjectSections/ProjectSectionThree';
import ProjectSectionFour from '../ProjectSections/ProjectSectionFour';
// import './Projects.css';

class Projects extends Component {
    render() { 
        return ( 
            <div>
                <section className="heroz w3-animate-left" style={{marginTop: '52px'}}>
                    <div className="overlayer">
                    <div className="hero-body">
                        <div className="container">
                            <div className="columns">
                                <div className="column is-one-third"></div>
                                <div className="column">
                                    <h1 className="title111">Projects</h1>
                                    <hr className="news"></hr>
                                </div>
                                <div className="column"></div>
                            </div>
                        </div>
                    </div>
                    </div>
                </section>
                <ProjectSection />
                <ProjectSectionTwo />
                <ProjectSectionThree />
                <ProjectSectionFour />

                <ScrollToTop showUnder={500} duration={1000} >
                    <img src={arrowUp} alt="Back to top" />
                </ScrollToTop>
            </div>
         );
    }
}
 
export default Projects;