import React, { Component } from 'react';
import './ProjectSection.css';

class ProjectSectionTwo extends Component {
    render() { 
        return ( 
            <div>
                <section style={{backgroundColor: 'rgb(179, 109, 18)', marginTop: '50px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-zoom">
                            <div className="column is-one-half">
                                <h3 style={{color: 'black'}}>Chachazele Sorghum Project</h3>
                                <hr className="new11"></hr> 
                            </div>
                            <div className="column">
                                <p className="mainText2">
                                    The Chachazela Sorghum Cooperative (CSC) is the second recipient of our Eastern Cape sorghum aggregation project.  
                                    It is also based in the Mbhashe Municipality and according to Stats SA, Mbashe municipality’s last measured economic 
                                    growth rate is -0.35%. <br></br>
                                    The dependency rate for this region is 85.5% and the unemployment rate is 42%, with youth unemployment sitting at 54%.
                                </p>

                                <br></br>

                                <p className="mainText2">
                                    The beneficiaries for CSC are 38 smallholder farmers and each farmer owns an average of 4.69 hectares of land. 
                                    The majority of the participants are women and although the youth participation is low (0.06%), 
                                    we plan on using the successes from the 2018/19 season as a base to attract more young people and women into the program.
                                    <br></br>  
                                    This project has enjoyed exceptional community involvement and commitment; 
                                    a lesson LMH will be taking to other projects.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
         );
    }
}
 
export default ProjectSectionTwo;