import React, { Component } from 'react';
import './ProjectSnippet.css';
import { Link } from 'react-router-dom';

class ProjectSnippet extends Component {
    render() { 
        return ( 
            <div>
                 <section style={{backgroundColor: 'rgb(179, 109, 18)', marginTop: '5px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-right">
                            <div className="column is-one-half">
                                <h1 style={{color: 'white'}}>Our Projects</h1>
                                <hr className="newer"></hr> 
                            </div>
                            <div className="column">
                                <p className="mainText2">Have a look at the successful projects we have conducted...</p>
                                <br></br>
                                <br></br>

                                <Link to="/projects">
                                    <button className="btn-cont">Read More</button>
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
         );
    }
}
 
export default ProjectSnippet;