import React, { Component } from 'react';
import './Home.css';
import ScrollToTop from 'react-scroll-up';
import arrowUp from '../../assets/up.png';
import ContactSection from '../ContactSection/ContactSection';
import '../../w3.css';
import HomeSnippet from '../HomeSnippet/HomeSnippet';
import ProjectSnippet from '../ProjectSnippet/ProjectSnippet';

class Home extends Component {
    render() { 
        return ( 
            <div>
                <section className="heros" style={{marginTop: '50px'}}>
                    <div className="hero-body">
                        <div className="container">
                        </div>
                    </div>
                </section>

                <ProjectSnippet />
                <HomeSnippet />
                <ContactSection />


                <ScrollToTop showUnder={500} duration={1000} >
                    <img src={arrowUp} alt="Back to top" />
                </ScrollToTop>
            </div>
         );
    }
}
 
export default Home;