import React, { Component } from 'react';
import './ProjectSection.css';
import Land from '../../assets/Clearance.jpg';

class ProjectSectionThree extends Component {
    render() { 
        return ( 
            <div>
                <section style={{backgroundColor: 'white', marginTop: '50px'}}>
                    <div className="container" style={{padding: '50px'}}>
                        <div className="columns w3-animate-zoom">
                            <div className="column is-one-half">
                                <h3 style={{color: 'black'}}>Black Wattle / Alien Invader (Acacia mearnsii) Eradication &amp; Processing</h3>
                                <hr className="new22"></hr>
                                <img className="cnt" src={Land} alt="land clearance" style={{width: '70%'}} /> 
                            </div>
                            <div className="column">
                                <p className="mainText11">
                                    The Mbashe and Mnquma local municipalites where LHM is implementing its farming projects is challenged by a massive invader 
                                    bush encroachment of the farm/arable land. This has a negative environmental and economic effect of the phenomenon 
                                    for the farming communities. 
                                    At the same time the bush encroachment constitutes an immense economic opportunity and energy asset.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                    The massive bush encroachment on 30% of the Mbashe &amp; Mngquma Local Municipalities is a challenge for both the local 
                                    economies and ecosystems. 
                                    The bush encroachment increase water shortage and decrease arable land and has led to 
                                    land productivity loss of more than 60%.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                    The intruder bushes are primarily the species <b>Acacia mearnsii</b>. De-bushing is a “win - win -win - win” situation.
                                    A win for small holder farmers: agricultural production in the area has diminished by 60%. 
                                    The restoration of these fields means that these smallholder farmers get an opportunity to be introduced to the main stream of commercial farming. 
                                    The project also enables their land to be productive.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                     A win for the environment: the benefits to the environment are advantageous in many ways, but what is crucial is a ground water recharge.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                    A win for Mbashe/Mnquma Labour: The harvest and biomass processing provides ample job opportunities and the targeted areas are mostly rural communities - approximately 200 jobs can be created.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                    A win for the South African energy supply: Biomass products are an important step towards independence from foreign energy. The harvesting of invader bush makes available the local biomass for home and industrial use.
                                </p>

                                <br></br>

                                <p className="mainText11">
                                     Our first team of workers (made up of experienced 20 tree fellers) have been employed and have been harvesting and preparing the tree trunks and barks since January 2019.
                                </p>
                            </div>
                        </div>
                    </div>
                </section>
            </div>
         );
    }
}
 
export default ProjectSectionThree;