import React, { Component } from 'react';
import './Header.css';
// import Logo from '../../assets/LM Logo.jpg';
import { Link } from 'react-router-dom';

class Header extends Component {

  constructor(props) {
      super(props);
      this.state = {isToggleOn: false};

      this.handleClick = this.handleClick.bind(this);
  }
  
  handleClick() {
      this.setState(prevState => ({
        isToggleOn: !prevState.isToggleOn
      }));
  }

  render() {

    let menuActive = this.state.isToggleOn ? 'is-active' : '';

      return (
        <div className="App">
            <div className="containter">
              <div className="navbar is-black is-fixed-top" role="navigation" style={{padding: '5px'}}>
                  <div className="navbar-brand">
                  <Link to="/">
                      <a className="navbar-item" style={{color: 'white'}}>
                          <strong>LM Holdings</strong>
                      </a>
                  </Link>

                      <span className={'navbar-burger burger' +menuActive} onClick={this.handleClick}>
                      <span></span>
                      <span></span>
                      <span></span>
                      </span>
                  </div>

                  <div className={"navbar-menu" +menuActive}>
                          <div className="navbar-end">
                              <Link to="/" className="navbar-item">Home</Link>
                              <Link to="/about" className="navbar-item">About</Link>
                              <Link to="/projects" className="navbar-item">Projects</Link>
                              <Link to="/articles" className="navbar-item">Articles</Link>
                              <Link to="/gallery" className="navbar-item">Gallery</Link>
                              <Link to="/contact" className="navbar-item">Contact</Link>
                          </div>
                      </div>
              </div>
            </div>
        </div>
      );
    }
  }

export default Header;
