import React, { Component } from 'react';
import './Section.css';


class SectionFive extends Component {
    render() { 
        return ( 
            <section style={{backgroundColor: 'white', marginTop: '50px'}}>
            <div className="container" style={{padding: '50px'}}>
                <div className="columns w3-animate-opacity">
                    <div className="column is-one-half">
                        <h2 style={{color: 'black'}}>NI LM Investments</h2>
                        <hr className="new2"></hr> 
                    </div>
                    <div className="column">
                        <p className="mainText">
                            This subsidiary deals with strategic agricultural investments including Horticulture, aquaculture, 
                            animal husbandry and agro-processing and packaging.
                        </p>
                    </div>
                </div>
            </div>
        </section>
         );
    }
}
 
export default SectionFive;